import React from 'react';


function welcomeBox(type) {
    return (
        <React.Fragment>
            <div id="welcomeBox">
                <div id="#welcomeBox-h1Div"><h1>Bem-Vindo ao <b>Arboedu</b>.web!</h1></div>
                <div><p><b>Caro {type},</b></p>
                    <p>Este  um estudo piloto que faz parte de uma pesquisa sobre
                    o uso de dispositivos mveis para execuo de aes educativas
                    visando  mudana da populao em relao  preveno de aboviroses,
                    como a Dengue, Zika e Chikungunya
                </p>
                </div>
            </div>
        </React.Fragment>
    )
}

export default welcomeBox